<template>
  <div class="foodMenuSearch">
    <div class="header">
      <div class="iconfont icon-jiantouzuo goBack-icon" @click="gotoBack()"></div>
      <div class="tp-search">
        <div class="tp-search-box">
          <div class="iconfont icon-sousuo"></div>
          <input type="text" v-model="tableNo" class="inputCla" placeholder="房间检索">
          <div class="iconfont icon-quxiao" @click="deletInput" v-show="showDelet"></div>
        </div>
        <div class="tp-right">
          <span class="search-btn" @click="searchFun()">搜索</span>
        </div>
      </div>
    </div>
    <div class="body">
      <div class="room-list" v-if="this.roomArr.length>0">
        <div
          class="room-item"
          v-for="(item, index) in this.roomArr"
          :key="index"
          @click="selectPeopleFun(item)"
        >
          <div class="room-text">楼栋：{{item.buildName+",房间:"+item.roomName}}</div>
          <div class="room-text">姓名:{{item.customerName}}</div>
        </div>
      </div>
      <div v-else-if="this.roomArr.length==0 && isShowNoResult" class="no-list">
        <div class="no-result-icon">
          <i class="iconfont icon-jiazaishibai"></i>
        </div>
        <p class="no-result-text">未查到结果</p>
      </div>
    </div>
  </div>
</template>
<script>
import moment from "moment";
import { mapState } from "vuex";
import { Indicator, Toast, MessageBox } from "mint-ui";
export default {
  data() {
    return {
      tableNo: "",
      showDelet: false,
      debounceTask: 500,
      roomArr: [],
      isShowNoResult: false,
      printNum: 0
    };
  },
  computed: {
    ...mapState({
      orderNum: state => state.food.orderNum,
      orderInfo: state => state.food.orderInfo
    })
  },
  mounted() {
    console.log("orderInfo", this.orderInfo);
  },
  methods: {
    //input删除键
    deletInput() {
      this.tableNo = "";
    },

    gotoBack() {
      this.$store.commit("submitOrderNum", {
        from: "shoppingCart",
        orderNum: this.orderNum
      });
      this.$router.push("/foodOrder");
    },
    searchFun() {
      // if (this.debounceTask !== undefined) clearTimeout(this.debounceTask);
      // this.debounceTask = setTimeout(() => {
      this.serachData(this.tableNo);
      //}, 1000);
    },
    serachData(val) {
      this.isShowNoResult = false;

      this.$http
        .get("/ohotel/router", {
          params: {
            method: "searchRoom",
            roomCode: this.tableNo
          },
          vv: 5
        })
        .then(res => {
          if (res.data.code == "0") {
            //console.log("res.data.data", res.data.data);
            this.isShowNoResult = true;
            this.roomArr = res.data.data;
          } else {
            this.msg(res.data.msg);
          }
        });
    },
    selectPeopleFun(item) {
      let msg = `是否将房账挂于${item.buildName}楼、${item.roomName}房间、${
        item.customerName
      }`;
      MessageBox.confirm(msg)
        .then(action => {
          this.account(item);
        })
        .catch(action => {});
    },
    account(item) {
      this.$http
        .get("/ohotel/router", {
          params: {
            method: "updateMaster",
            accnt: this.orderNum,
            presta: 0,
            payCode: "TAIKANG-" + 5,
            outId: 1,
            orderNo: 1,
            throwAcc: 1,
            throwAccData: {}
          },
          vv: 5
        })
        .then(res => {
          console.log("denglu:", res);
          if (res.data.code == "0") {
            //this.pushOrder();
            if (
              this.menuInfo.eatTime <
              moment()
                .add(10, "minute")
                .format("YYYY-MM-DD HH:mm")
            ) {
              this.pushOrder();
            } else {
              this.print();
              this.$router.push("/foodOrder");
            }
          } else {
            this.msg(res.data.msg);
          }
        });
    },
    pushOrder() {
      this.$http
        .get("/ohotel/router", {
          params: {
            method: "sendOrder",
            v: "2.0",
            accnt: this.orderNum
          },
          vv: 5
        })
        .then(res => {
          if (res.data.code == "0") {
            //this.msg(res.data.msg);
            this.msg("已下厨");
            this.$router.push("/foodOrder");
          } else {
            this.msg2(res.data.msg);
          }
        });
    },
    print() {
      //console.log("dayin:", this.menuInfo);
      this.printNum = 0;
      printDcData(this.printSuccess, this.printError, this.orderInfo);
    },
    printSuccess() {
      if (this.printNum < 1) {
        this.printNum++;
        printDcData(this.printSuccess, this.printError, this.orderInfo);
      }
    },
    printError(v) {
      MessageBox.confirm("打印出错：" + v + ",继续打印？")
        .then(action => {
          printDcData(this.printSuccess, this.printError, this.orderInfo);
        })
        .catch(action => {});
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    },
    msg2(msg) {
      MessageBox.alert(msg);
    }
  },
  watch: {
    tableNo() {
      if (this.tableNo) {
        this.showDelet = true;
      } else {
        this.showDelet = false;
      }
    }
  }
};
</script>
<style lang="less" scoped>
.foodMenuSearch {
  .header {
    padding-left: 0.347rem;
    padding-right: 11px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #393c41;
    .goBack-icon {
      color: #fff;
    }
    .tp-search {
      flex: 1;
      display: flex;
      justify-content: space-between;
      flex-direction: row;
      padding-left: 0.373rem;
      background-color: #393c41;

      .tp-search-box {
        flex: 1;
        height: 0.853rem;
        margin-top: 0.211rem;
        margin-bottom: 0.211rem;
        display: flex;
        justify-content: flex-start;
        flex-direction: row;
        align-items: center;
        background: #f6f8f9;
        border-radius: 1.3rem;
        .icon-sousuo {
          color: #cccccc;
          margin-left: 0.27rem;
          font-size: 0.37rem;
        }
        .icon-quxiao {
          color: #cccccc;
          font-size: 0.32rem;
          margin-right: 0.27rem;
        }

        .inputCla {
          border: none;
          background: #f6f8f9;
          outline: none;
          font-size: 0.32rem;
          width: 85%;
          margin-left: 0.27rem;
        }
      }
      .tp-right {
        margin-left: 0.4rem;
        .search-btn {
          line-height: 4;
          color: #fff;
        }
      }
    }
  }
  .body {
    padding: 0.133rem 0.133rem;
    .room-list {
      .room-item:last-child {
        border: none;
      }
      .room-item {
        border-bottom: 1px solid #eee;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-right: 0.587rem;
        height: 1.253rem;

        .room-text {
          font-size: 0.373rem;
          color: #000000;
        }
      }
    }
    .no-list {
      font-size: 0.32rem;
      .no-result-icon {
        text-align: center;
        padding-top: 2.667rem;
        .iconfont {
          font-size: 1.28rem;
        }
      }
      .no-result-text {
        text-align: center;
      }
    }
  }
}
</style>



